.IntroMain {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.gradient-background {
  background-image: linear-gradient(
      to bottom,
      rgba(12, 11, 11, 0),
      rgba(0, 0, 0, 0.1),
      rgba(0, 0, 0, 0.3),
      rgba(0, 0, 0, 0.4),
      rgba(0, 61, 5, 0.6),
      rgba(1, 44, 7, 0.8),
      rgba(0, 29, 8, 0.9),
      rgba(0, 0, 0, 1)
    ),
    url("../../../public/Images/Intro-1.jpg");
}

.ButtonContainer {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-top: 56rem;
}

.lemon {
  background-color: var(--lemon);
  color: #fff;
}

.logo-cont {
  position: absolute;
  top: 2rem;
  left: 3rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}

.imsu-logo {
  width: 8rem;
  height: 8rem;
}

.logo-text {
  color: #fff;
  top: 2rem;
  left: 3rem;
  font-size: 2.4rem;
  justify-content: flex-start;
}

.bottom-aside-text {
  position: absolute;
  bottom: 2rem;
  left: 3rem;
  font-size: 1.4rem;
}

@media (max-width: 567px) {
  .btn {
    font-size: 2.3rem;
  }
}
