/* **********NOTE UNAVIALABLE ********** */
.noteUnavailableContainer {
  width: 100%;
  height: 100%;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
}
.noteUnavailableContainer img {
  width: 90%;
  height: 10%;
  align-self: center;
  justify-self: center;
}
.noteUnavailableContainer p {
  text-align: center;
  font-size: 3.4rem;
  line-height: 5.5rem;
  font-weight: 600;
}

.noteUnavailableContainer span {
  color: var(--darkSecondary);
}
