/* *******PROMPT.JS*********** */
.promptContainer {
  width: 70%;
  height: auto;
  padding: 1.5rem;
  border-radius: 7px;
  box-shadow: 0 4px 8px 0 rgba(143, 139, 139, 0.2),
    0 6px 20px 0 rgba(151, 148, 148, 0.19);
  z-index: 10;
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -16%);
  z-index: 10;
  background-color: #fff;
}
.promptImage {
  background-image: url("../../public/Images/Intro-4.jpg");
  height: 20rem;
  width: 100%;
}
.promptContainer {
  display: flex;
  flex-direction: column;
  gap: 1.8rem;
}
.promptContainer h2 {
  text-align: center;
  font-size: 2rem;
}

.promptContainer p {
  font-size: 1.7rem;
}
.promptContainer button {
  background-color: var(--secondary);
  color: #fff;
  border: none;
  border-radius: 9px;
  padding: 0.9rem;
}
.promptContainer p {
  text-align: center;
  line-height: 3rem;
}
.promptClose {
  position: absolute;
  background-color: #fff;
  border-radius: 50%;
  width: 4rem;
  color: #222;
  padding: 0.9rem;
  font-size: 2.3rem;
  font-weight: 600;
  text-align: center;
}
/* *******PROMPT.JS Activate*********** */

/* **** Activation SuccessFul */
.activation-success img {
  width: 100%;
  height: 60%;
  object-fit: cover;
  padding-top: 2rem;
}
.activation-success-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  font-size: 2.4rem;
  transform: translateY(-60px);
  background-color: #fff;
}
.activation-success-content h3 {
  font-weight: 500;
  font-size: 3.2rem;
  color: var(--secondary);
}
.activation-success-content button {
  padding: 0.9rem;
  background-color: var(--secondary);
  width: 50%;
  color: #fff;
  border-radius: 20px;
}

.activation-success-content p {
  font-size: 1.8rem;
}
/* **** Activation SuccessFul */
